import React, {useState, useEffect} from 'react';
import { CssBaseline, Paper, Stepper, Step, StepLabel, Typography, CircularProgress, Divider, Button } from '@material-ui/core';
import useStyles from './styles';
import AddressFrom from '../AddressFrom';
import PaymentForm from '../PaymentForm';
import {commerce} from '../../../lib/commerce';
import { Link } from 'react-router-dom';

const steps = ['Shipping address', 'Payment details']

const Checkout = ({ cart, order, onCaptureCheckout, error }) => {
    const classes = useStyles();
    const [checkoutToken, setCheckoutToken] = useState(null);
    const [shippingData, setShippingData] = useState({});
    const [isFinished, setIsFinished] = useState(false);

    const [activeStep, setActiveStep] = useState(0);

    useEffect(()=>{
        const generateToken = async () =>{
            try{
               const token = await commerce.checkout.generateToken(cart.id, {type:'cart'});
               setCheckoutToken(token);
            }
            catch(error){

            }
        }
        generateToken();
    },[cart]);

    const nextStep = () =>setActiveStep((prevActiveStep) => prevActiveStep + 1);
    const backStep = () =>setActiveStep((prevActiveStep) => prevActiveStep - 1);

    const next = (data) =>{
        setShippingData(data);
        nextStep();
    }

    const timeout = () => {
        setTimeout(()=>{
            setIsFinished(true);
        },3000)
    }


    let Confirmation = () => order.customer ? (
        <>
            <div>
                <Typography variant="h5">Thank you for your Purchase, {order.customer.firstname} {order.customer.lastname}</Typography>
                <Divider className={classes.divider} />
                <Typography variant="subtitle1">Order ref: {order.customer_reference}</Typography>
           </div>  

           <br />
           <Button component={Link} to="/" variant="outlined" type="button">Back to Home</Button>
        </>
    ) :isFinished ? (
        <>
        <div>
            <Typography variant="h5">Thank you for your Purchase</Typography>
            <Divider className={classes.divider} />
            <Typography variant="subtitle1">Order ref:</Typography>
       </div>  

       <br />
       <Button component={Link} to="/" variant="outlined" type="button">Back to Home</Button>
    </>   
    )
     :
      (
        <div className={classes.spinner}>
            <CircularProgress />
        </div>
    )

    if(error) {
        <>
                <Typography variant="h5">Error: {error}</Typography>
                <br />
                <Button component={Link} to="/" variant="outlined" type="button">Back to Home</Button>

        </>
    }

    const Form = () => activeStep === 0
    ? <AddressFrom checkoutToken={checkoutToken} next={next} />
    : <PaymentForm shippingData={shippingData} onCaptureCheckout={onCaptureCheckout}nextStep={nextStep} checkoutToken={checkoutToken} backStep={backStep} timeout={timeout} />

    return (
        <>
        <CssBaseline />
          <div className={classes.toolbar} /> 
          <main className={classes.layout}>
              <Paper className={classes.paper}>
                  <Typography variant="h4" align="center">Checkout</Typography>
                  <Stepper activeStep={activeStep} className={classes.stepper}>
                      {steps.map((step)=>(
                          <Step key={step}>
                            <StepLabel>{step}</StepLabel>
                          </Step>
                      ))}
                  </Stepper>

                  {activeStep === steps.length ? <Confirmation /> : checkoutToken && <Form /> }
              </Paper>
          </main> 
        </>
    )
}

export default Checkout
